import Vuex from "vuex";
import Vue from 'vue';
//    import
import createPersistedState from 'vuex-persistedstate';

//    import

Vue.use(Vuex);

// store 
export default new Vuex.Store({
    state: { token: null, user_email: null },
    //state   getter 
    getters: {
        isLogin(state){ return state.token == null ? false : true ; },
        userEmail(state){ return state.userEmail; },
        token(state){ return state.token; }
    },
    //state 
    mutations: {  // commit    .
        setToken(state, _token){ state.token = _token; },
        setUserEmail(state, _userEmail){ state.userEmail = _userEmail; }
    },
    // mutations    dispatch  .
    actions: { // dispatch    .
        setToken:({commit} , _token) => { commit('setToken' , _token); },
        user_email:({commit} , _userEmail) => { commit('setUserEmail' , _userEmail); }
    },
    plugins: [ createPersistedState({})],
})


